.window {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}



.homepage_main {
  background: linear-gradient(260deg, #4BA4F2, #6bf2e5);
  background-size: 200% 200%;
  -webkit-animation: back-ani 5s ease infinite;
  -moz-animation: back-ani 5s ease infinite;
  animation: back-ani 5s ease infinite;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  transition-duration: 1s;

}


@-webkit-keyframes back-ani {
  0%{background-position:0 50%}
  50%{background-position:100% 50%}
  100%{background-position:0 50%}
}
@-moz-keyframes back-ani {
  0%{background-position:0 50%}
  50%{background-position:100% 50%}
  100%{background-position:0 50%}
}
@keyframes back-ani {
  0%{background-position:0 50%}
  50%{background-position:100% 50%}
  100%{background-position:0 50%}
}


